body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
	font-family: "mrDafoe";
	src: url(/static/media/MrDafoe-Regular.93322e09.ttf);
}
.lead
{
  text-align: center;
}


.logo
{
  word-spacing: 10px;
  letter-spacing: 1px;
  font-family: "mrDafoe","Times New Roman", serif;
}
.jumbotron {
  min-height: 200px;
  padding-top: 50px;
  background: #f7f7f7;
}

.jumbotron a
{  color:rgb(50, 104, 145) !important;}

@font-face {
	font-family: "mrDafoe";
	src: url(/static/media/MrDafoe-Regular.93322e09.ttf);
}
/*font-family: "chatfont","Times New Roman", serif;  */
.logo
{
  text-align: center !important;
  word-spacing: 10px;
  letter-spacing: 1px;
  font-family: "mrDafoe","Times New Roman", serif;
}
nav.navbar {
  margin-bottom: 0;
  background-color: #ffffff !important;
}

div.thumbnail {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 95%;
  width: 95%;
  padding-top: 0%;


}
.thumbnailimg
{
  width: 100%;
  height: 100%;
  border-radius: 5px;
  border: 1px solid #ced4da;
}

small{
  letter-spacing: 1px;
}

.recipeTitle
{
  letter-spacing: -1px;
}

.searchResultsEntries
{
  background-color: #ffffff;
}

.recipeLink
{ background-color: rgb(50, 104, 145);
  border:1px;
  letter-spacing: -1px;}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

.resultsTitle
{
  background-color: #f7f7f7 !important;
}

form
{
  width:100%;
}
@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

